<template>
  <el-table
      :data="tableData"
      stripe
      style="width: 100%">
    <el-table-column
        prop="index"
        label="序号"
        width="180">
    </el-table-column>
    <el-table-column
        prop="student"
        label="学生名"
        width="180">
    </el-table-column>
    <el-table-column
        prop="course"
        label="课程名"
        width="180">
    </el-table-column>
    <el-table-column
        prop="teacher"
        label="教师名">
    </el-table-column>
    <el-table-column
        prop="score"
        label="分数">
      <template slot-scope="scope">
        <span>{{scope.row.score===-1?'暂无':scope.row.score}}</span>
      </template>
    </el-table-column>
    <el-table-column
        prop=""
        label="操作">
      <template slot-scope="scope">
        <el-button size="mini" type="primary" plain :data-id="scope.row.courseId"
                   @click="updateScore(scope.row.courseId,scope.row.stuId)"
                   class="addScore">修改
        </el-button>
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
  // import updateCourseBox from "./updateTeacherCoursesBox.vue";

  export default {
    name: 'allCourses',
    methods: {
      updateScore(courseId,stuId) {
        this.$prompt('请输入分数', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          inputPattern: /^[0-9]+$/,
          inputErrorMessage: '格式不正确',
          inputValidator: function (v) {
            if (v < 0 || v > 150) {
              return '分数超过限制'
            }
          }
        }).then(({value}) => {
          this.$message({
            type: 'success',
            message: '分数是: ' + value
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '取消输入'
          });
        });
      }
    },
    data() {
      return {
        tableData: [{
          index: '1',
          student: '王小虎',
          course: '第三方',
          teacher: '张5',
          score: 90,
          courseId: 1,
          stuId: 1,
        }, {
          index: '2',
          student: '王小虎2',
          course: '第三方',
          teacher: '张5',
          score: 45,
          courseId: 2,
          stuId: 2,
        }, {
          index: '3',
          student: '王小虎',
          course: '第三方',
          teacher: '张5',
          score: 33,
          courseId: 3,
          stuId: 3,
        }, {
          index: '4',
          student: '王小虎',
          course: '第三方',
          teacher: '张5',
          score: -1,
          courseId: 4,
          stuId: 4,
        }]
      }
    }
  }
</script>